body, .root {
  font-family: -apple-system, BlinkMacSystemFont, 'Lato', sans-serif;
  height: 100vh;
  margin: 0;
  padding: 0;
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  color: #181818;
  color: var(--color-black);
  font-size: 10px;
}

@media (min-width: 1921px) {
  html, body {
    font-size: 13px;
  }
}

:root {
  --color-black: #181818;

  --height-header: 60px;
  --width-list: 20rem;
}

ul {
  list-style: none;
}

.App {
  height: 100%;
  position: absolute;
  width: 100%;
}

/* Header */
header {
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 2px 6px rgb(168, 168, 168); 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-height: 60px;
  max-height: var(--height-header);
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  z-index: 1;
}

#logo {
  color: #181818;
  color: var(--color-black);
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
}

header button {
  background: radial-gradient(circle at 10%, #D1C3FF, #BAA5FF);
  border: 0;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  padding: 10px;
  position: absolute;
  right: 40px;
  width: 100px;
}

/* This panel splits the screen into two peices */
#main-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: calc(100% - 60px);
  height: calc(100% - var(--height-header));
  overflow: hidden;  
}

ul#list {
  border-right: dashed 2px #707070;
  height: 100%;
  overflow: auto;
  padding: 10px;
  width: 20rem;
  width: var(--width-list);
}

/* This is the container of the Categories */
#board {
  -webkit-align-items: flex-start;
          align-items: flex-start;
  display: grid;
  font-size: 1.3rem;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 10px;
  gap: 10px;
  width: 100%;  
}

/* Card */
.card {
  background-color: white;
  border: solid 1px #8b8b8b;
  border-radius: 10px;
  box-shadow: 0 2px 2px rgb(226, 226, 226);
  cursor: move;
  margin-bottom: 10px;
  padding: 10px;
  text-align: center;
}

.card.minimized {
  -webkit-align-items: center;
          align-items: center;
  border-radius: 0;
  box-shadow: none;
  display: -webkit-flex;
  display: flex;
  height: 1.2rem; width: 200px;
  margin-bottom: 10px;
  padding-left: 5px;
  position: relative;
  text-align: start;
}

.card.minimized h4 {
  display: inline;
  font-weight: 100;
  font-size: 80%;
  margin: 0; 
}

.card.minimized .desc-container {
  margin-left: auto;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.card.minimized .desc-btn {
  background-color: #BAA5FF;
  border: 0;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  height: 1.2rem; width: 1.2rem;
}

.card.minimized .desc-pop {
  -webkit-align-items: center;
          align-items: center;
  background-color: white;
  border: solid 4px #BAA5FF;
  box-shadow: 0 2px 6px rgb(168, 168, 168);
  position: absolute;
  left: 215px;
  margin: 0;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
  z-index: 10;
  width: 150px;
}

.card.minimized .desc-pop p {
  margin: 0;
  margin-left: 5px;
}

.card.minimized .desc-pop::after {
  content: '';
  height: 0; width: 0;
  border-top: 15px solid transparent;
  border-right: 15px solid #BAA5FF;
  border-bottom: 15px solid transparent;
  position: absolute;
  left: -17px; top: 0;
}

/* Category */
.category {
  -webkit-align-items: center;
          align-items: center;
  border: solid 2px #8b8b8b;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgb(0, 0, 0, 16%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-height: 25rem;
  overflow-y: auto;
  position: relative;
  width: 240px;
}

.category.max-height {
  height: 25rem;
}

.category h3 {
  cursor: pointer;
  font-size: 1.5rem;
  text-decoration: underline;
}

.category h3::first-letter {
  text-transform: uppercase;
}

.category ul {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10px;
}

.category li {
  margin: 0;
}

.category .title-input {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 3rem;
  margin: .5rem;
  position: relative;
}

.category .title-input button {
  background: 0;
  background-image: url(./sort/static/media/verified.7a0423bd.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  cursor: pointer;
  height: 100%; width: 2rem;
  margin-left: .5rem;
}

.category .title-input input {
  border: solid 1px #707070;
  border-radius: 5px;
  height: 100%;
}

.category .drop-to-add {
  -webkit-align-items: center;
          align-items: center;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 1.3rem;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%; width: 100%;
  left: 0; top: 0; 
  opacity: .8;
  position: absolute;
  z-index: 10;
}

.category .drop-to-add img {
  height: auto; width: 50%;
}

/* This is shown when the user hovers a card to the board */
.category.drop-to-create {
  -webkit-align-items: center;
          align-items: center;
  background: 0;
  border-style: dashed;
  box-shadow: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 25rem;
  opacity: .8;
}

.category.drop-to-create img {
  height: auto; width: 50%;
  opacity: .7;
} 

.category.drop-to-create p {
  font-size: 1.3rem;
}

/* Message (thanks, not found) screen */

.message-screen {
  -webkit-align-items: center;
          align-items: center; 
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh; width: 100vw;
  -webkit-justify-content: center;
          justify-content: center;
  left: 0; top: 0;
  overflow: hidden;
  position: absolute;
  z-index: 100;
}

.message-screen img {
  height: 50vh; width: auto;
  padding-bottom: 3rem;
}

.message-screen h2 {
  color: #181818;
  color: var(--color-black);
  font-size: 3rem;
}

.message-screen h2::first-letter {
  text-transform: uppercase; 
}

/* Onboarding screen */

.on-boarding-screen {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh; width: 100vw;
  left: 0; top:0;
  position: absolute;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 100;
}

.on-boarding-screen img {
  height: 40vh; width: auto;
}

.on-boarding-screen h2 {
  font-size: 3rem;
}

.on-boarding-screen .arrow {
  height: 10rem; width: auto;
}

.on-boarding-screen p {
  font-size: 1.5rem;
  text-align: center;
  width: 30rem;
}

.on-boarding-screen .list-explainer {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  left: 20rem;
  left: var(--width-list); top: 60px; top: var(--height-header);
  padding: 2rem;
  position: absolute;
}

.on-boarding-screen .list-explainer .arrow {
  margin-right: 3rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.on-boarding-screen .finish-explainer {
  -webkit-align-items: flex-end;
          align-items: flex-end;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  right: 0; top: 60px; top: var(--height-header);
  padding: 2rem;
  position: absolute;
}

.on-boarding-screen .finish-explainer .arrow {
  margin-right: 2rem;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.on-boarding-screen .finish-explainer p {
  text-align: right;
}
